import axios from 'axios';
import { GameRequest, GameResponse, TileInfo } from '../types/mahjong';

const API_BASE_URL = process.env.REACT_APP_API_URL || 'http://localhost:8000';

// axios
const api = axios.create({
  baseURL: API_BASE_URL,
  timeout: 10000,
  headers: {
    'Content-Type': 'application/json',
  },
});

// 
api.interceptors.request.use(
  (config) => {
    console.log('API:', config.method?.toUpperCase(), config.url);
    return config;
  },
  (error) => {
    console.error('API:', error);
    return Promise.reject(error);
  }
);

// 
api.interceptors.response.use(
  (response) => {
    console.log('API:', response.status, response.config.url);
    return response;
  },
  (error) => {
    console.error('API:', error.response?.status, error.response?.data);
    return Promise.reject(error);
  }
);

// API
export class MahjongAPI {
  /**
   * 
   */
  static async analyzeGame(request: GameRequest): Promise<GameResponse> {
    try {
      const response = await api.post<GameResponse>('/api/mahjong/analyze', request);
      return response.data;
    } catch (error) {
      console.error(':', error);
      throw new Error('');
    }
  }

  /**
   * 
   */
  static async getTileCodes(): Promise<TileInfo[]> {
    try {
      const response = await api.get<{ tiles: TileInfo[] }>('/api/mahjong/tile-codes');
      return response.data.tiles;
    } catch (error) {
      console.error(':', error);
      throw new Error('');
    }
  }

  /**
   * 
   */
  static async healthCheck(): Promise<boolean> {
    try {
      const response = await api.get('/health');
      return response.data.status === 'healthy';
    } catch (error) {
      console.error(':', error);
      return false;
    }
  }

  /**
   * API
   */
  static async getApiInfo(): Promise<any> {
    try {
      const response = await api.get('/');
      return response.data;
    } catch (error) {
      console.error('API:', error);
      throw new Error('API');
    }
  }
}

// WebSocket
export class WebSocketManager {
  private ws: WebSocket | null = null;
  private reconnectTimeout: NodeJS.Timeout | null = null;
  private readonly reconnectDelay = 3000; // 3
  private readonly maxReconnectAttempts = 5;
  private reconnectAttempts = 0;

  constructor(
    private clientId: string,
    private onMessage: (data: any) => void,
    private onConnect: () => void,
    private onDisconnect: () => void,
    private onError: (error: Event) => void
  ) {}

  connect(): void {
    try {
      const wsUrl = `${API_BASE_URL.replace('http', 'ws')}/api/mahjong/ws/${this.clientId}`;
      this.ws = new WebSocket(wsUrl);

      this.ws.onopen = () => {
        console.log('WebSocket');
        this.reconnectAttempts = 0;
        this.onConnect();
      };

      this.ws.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          console.log('WebSocket:', data);
          this.onMessage(data);
        } catch (error) {
          console.error('WebSocket:', error);
        }
      };

      this.ws.onclose = () => {
        console.log('WebSocket');
        this.onDisconnect();
        this.attemptReconnect();
      };

      this.ws.onerror = (error) => {
        console.error('WebSocket:', error);
        this.onError(error);
      };
    } catch (error) {
      console.error('WebSocket:', error);
      this.onError(error as Event);
    }
  }

  private attemptReconnect(): void {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      console.log(`... (${this.reconnectAttempts}/${this.maxReconnectAttempts})`);
      
      this.reconnectTimeout = setTimeout(() => {
        this.connect();
      }, this.reconnectDelay);
    } else {
      console.log('');
    }
  }

  sendMessage(message: any): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify(message));
    } else {
      console.warn('WebSocket');
    }
  }

  disconnect(): void {
    if (this.reconnectTimeout) {
      clearTimeout(this.reconnectTimeout);
      this.reconnectTimeout = null;
    }
    
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
  }

  isConnected(): boolean {
    return this.ws !== null && this.ws.readyState === WebSocket.OPEN;
  }
}

export default api; 