import React from 'react';
import { motion } from 'framer-motion';

interface SimpleSourceIndicatorProps {
  sourcePlayer: number;
  currentPlayer: number;
  className?: string;
}

const SimpleSourceIndicator: React.FC<SimpleSourceIndicatorProps> = ({ 
  sourcePlayer, 
  currentPlayer,
  className = ''
}) => {
  // 
  const getSourceLabel = (): string => {
    if (sourcePlayer === currentPlayer) return '';
    
    // 
    const diff = (sourcePlayer - currentPlayer + 4) % 4;
    const labels = ['', '', '', ''] as const;
    return labels[diff] || '';  // ""
  };

  // 
  const getTooltipText = (): string => {
    const label = getSourceLabel();
    if (label === '') return '';
    return `${label}`;
  };

  return (
    <motion.div
      className={`w-4 h-4 bg-red-600 text-white text-xs font-bold rounded-sm flex items-center justify-center shadow-md z-30 ${className}`}
      initial={{ scale: 0, opacity: 0 }}
      animate={{ scale: 1, opacity: 1 }}
      transition={{ 
        delay: 0.2, 
        type: "spring", 
        stiffness: 300,
        damping: 20
      }}
      title={getTooltipText()}
    >
      {getSourceLabel()}
    </motion.div>
  );
};

export default SimpleSourceIndicator; 